import React from "react";
import NavigationBar from "../Componets/NavigationBar";
import {
  Box,
  Container,
  Grid,
  Card,
  CardContent,
  CardHeader,
  CardMedia,
  Avatar,
  Divider,
  Typography,
  IconButton,
} from "@mui/material";
import { styled } from "@mui/system";
import WorkIcon from "@mui/icons-material/Work";
import CodeIcon from "@mui/icons-material/Code";
import GitHubIcon from "@mui/icons-material/GitHub";

const StyledCard = styled(Card)({
  minHeight: 300,
  borderRadius: 12,
  boxShadow: "0 8px 20px rgba(0, 0, 0, 0.2)",
  backgroundColor: "#fff",
  transition: "transform 0.3s",
  "&:hover": {
    transform: "scale(1)",
  },
});

const StyledAvatar = styled(Avatar)({
  width: 56,
  height: 56,
});

const CustomTypography = styled(Typography)({
  fontFamily: "'Roboto', sans-serif",
  fontWeight: 300,
  letterSpacing: "0.5px",
  color: "#333",
});

function PortfolioPage() {
  return (
    <Box sx={{ flexGrow: 1, backgroundColor: "#f4f6f8", minHeight: "100vh" }}>
      <NavigationBar />
      <Container sx={{ paddingTop: 5 }}>
        <Grid container spacing={4}>
          <Grid item xs={12}>
            <Card
              sx={{
                borderRadius: 3,
                boxShadow: "0 8px 20px rgba(0, 0, 0, 0.2)",
              }}
            >
              <CardHeader
                avatar={
                  <StyledAvatar sx={{ backgroundColor: "#3f51b5" }}>
                    <WorkIcon />
                  </StyledAvatar>
                }
                title={
                  <CustomTypography variant="h5">Experience</CustomTypography>
                }
              />
              <CardContent>
                <Experience
                  title="Cyber Security Intern"
                  company="DSO National Laboratories"
                  duration="Jun 2022 - Jul 2022"
                  description="Worked on a series of cybersecurity CTF exercises and learnt about basic concepts in cybersecurity such as buffer overflow."
                />
                <DescDivider />
                <Experience
                  title="Machine Learning Intern"
                  company="DSTA"
                  duration="May 2024 - Aug 2024"
                  description="Anomaly detection in LTE networks. Created an AI model and written an article."
                />
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12}>
            <StyledCard>
              <CardHeader
                avatar={
                  <StyledAvatar sx={{ backgroundColor: "#f50057" }}>
                    <CodeIcon />
                  </StyledAvatar>
                }
                title={
                  <CustomTypography variant="h5">Projects</CustomTypography>
                }
              />
              <CardContent>
                <Grid container spacing={4}>
                  <Grid item xs={12} sm={6} md={4}>
                    <Project
                      title="Project A"
                      type="Web Development"
                      info="Developed a full-stack web application using React and Node.js."
                      image="https://via.placeholder.com/400x300" // Replace with actual image URL
                      githubLink="https://github.com/username/project-a" // Replace with actual GitHub URL
                    />
                  </Grid>
                  <Grid item xs={12} sm={6} md={4}>
                    <Project
                      title="Project B"
                      type="Machine Learning"
                      info="Built a machine learning model to detect anomalies in network traffic."
                      image="https://via.placeholder.com/400x300" // Replace with actual image URL
                      githubLink="https://github.com/username/project-b" // Replace with actual GitHub URL
                    />
                  </Grid>
                  {/* Add more projects as needed */}
                </Grid>
              </CardContent>
            </StyledCard>
          </Grid>
        </Grid>
      </Container>
    </Box>
  );
}

export default PortfolioPage;

function Experience({ title, company, duration, description }) {
  return (
    <Box sx={{ marginBottom: 2 }}>
      <CustomTypography variant="h6">{title}</CustomTypography>
      <CustomTypography variant="subtitle1" color="textSecondary">
        {company}  {duration}
      </CustomTypography>
      <CustomTypography variant="body1" sx={{ marginTop: 1 }}>
        {description}
      </CustomTypography>
    </Box>
  );
}

function Project({ title, type, info, image, githubLink }) {
  return (
    <Card
      sx={{
        minHeight: 300,
        borderRadius: 5,
        boxShadow: "0 4px 15px rgba(0, 0, 0, 0.1)",
        transition: "transform 0.3s",
        "&:hover": { transform: "scale(1.05)" },
      }}
    >
      <CardMedia component="img" height="140" image={image} alt={title} />
      <CardContent>
        <CustomTypography variant="h6">{title}</CustomTypography>
        <CustomTypography variant="subtitle1" color="textSecondary">
          {type}
        </CustomTypography>
        <CustomTypography variant="body1" sx={{ marginTop: 1 }}>
          {info}
        </CustomTypography>
        <IconButton
          href={githubLink}
          target="_blank"
          rel="noopener noreferrer"
          sx={{ marginTop: 2 }}
        >
          <GitHubIcon />
        </IconButton>
      </CardContent>
    </Card>
  );
}

function DescDivider() {
  return <Divider sx={{ marginY: 2 }} />;
}

